import React from "react";
import {connect} from "react-redux";
import LogoutConfirmationModal from "eshop/modules/auth/components/LogoutConfirmationModal";
import ProcessAlertModal from "eshop/modules/auth/components/ProcessAlertModal";
import OraTooltip from "eshop/modules/core/components/ui/Tooltip"
import {requestLoggedCustomerData, requestLoggedCustomerDataAndCheckExistence} from "eshop/modules/auth/actions/api"
import {getDeliveryAndPaymentStep, getIsCartStep, getIsCustomerDataStep} from "eshop/modules/checkout/selectors"
import {
    getLoggedCustomerData,
    getRegisterBillingAccountPopupB2B,
    isLogged
} from "eshop/modules/auth/selectors/authorization";
import {hasEntries, hasOnlyAssignment, isCartFix, isCartMobile} from "eshop/modules/cart/selectors"
import {isLandingPage, isPreLandingPage} from "eshop/modules/fix/selectors/root"
import {
    hideLogoutConfirmationModal,
    safeLogOut,
    showMobileAccountSelection,
} from "eshop/modules/auth/actions/authorization";
import MulticartCustomerLoginLink from 'eshop/modules/checkout/components/customer/MulticartCustomerLoginLink';
import {isAccessoryListPage, isProductDetailsPage, isProductListPage} from "eshop/modules/simfree/selectors"
import {updateIsLandingPage} from "eshop/modules/fix/actions/offers";

class LoggedCustomerBarComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state={isFix:false};
        if ((this.props.descriptions && this.props.descriptions.isFix)) {
            this.state={isFix:true};
            console.log("IS FIX LP");
        }
    }

    componentDidMount() {
        console.log("LoggedCustomerBarComponent GET LOGGED DATA");
        if (this.isFix()) {
            console.log("LoggedCustomerBarComponent FOR FIX");
            this.props.requestLoggedCustomerData();
        } else {
            this.props.requestLoggedCustomerDataAndCheckExistence();
        }
    }

    isFix(){
        return this.props.isFix || !!this.state.isFix;

    }
    _getAccountCode() {
        return (!!this.props.loggedCustomerData.accountCode && this.props.loggedCustomerData.accountCode !== "0") ? this.props.loggedCustomerData.accountCode : "Nowe Konto";
    }

    _renderTooltipLabel() {
        if (this.isFix()) {
            return (<span className="u-hide">{this._getAccountCode()}</span>);
        }
        return (
            <span>
                <span className="u-margin-right-s"> konto: </span>
                <span className="u-margin-right-s u-font-bold"> {this._getAccountCode()} </span>
            </span>
        );
    }

    _renderLogout() {
        if (!(!!this.props.loggedCustomerData.isOnlineCookie))
            return (
                <div className="l-col l-col-3  u-text-right">
                    <a href="#" onClick={this.props.safeLogOut}>Wyloguj</a>
                </div>);
        if ((this.props.showChangeAccount && !this.isFix()))
            return (
                <div className="l-col l-col-3  u-text-right">
                    <a href="#" onClick={this.props.showMobileAccountSelection}>Zmień konto</a>
                </div>);
        return null
    }

    render() {

        if ((this.isFix() && this.props.channels.sales !== "WWW")) {
            return null;
        }

        let showLogin = !this.props.isLogged && this.props.channels.sales === "WWW" && (this.props.isFixLP || this.props.isCustomerDataStep || this.props.isCartStep || ($("#offerTypeSelectionComponent").length && this.props.isCartMobile));
        if (showLogin || this.props.isLogged) {
            let tooltipContent = this.props.descriptions && this.props.descriptions.loggedCustomerTooltip || "Do faktury wskazanego przez Ciebie numeru, dołączone będą pozostałem numery umieszczone w koszyku. Będziesz otrzymywał jeden rachunek dotyczący należności z wielu numerów abonenckich zgrupowanych na wspólnym koncie, jak również będzie mógł skorzystać z promocji/rabatów związanych z łączeniem usług.";

            let addClassPages = !!(!this.props.isFixLP && !this.props.isCartStep && !this.props.isCustomerDataStep && !this.props.isProductListPage && !this.props.isAccessoryListPage && !this.props.isProductDetailsPage && !this.props.isDeliveryAndPaymentStep);

            let addClass = !!(this.props.isLogged && addClassPages);

            let addBoxShadowClass = !!(!this.props.isFixLP && !this.props.isCartStep && !this.props.isCustomerDataStep && !this.props.isDeliveryAndPaymentStep);
            let bgColorClass = " g-white1-bg ";
            if (addClassPages) {
                bgColorClass = " g-gray1-bg ";

            }
            return (
                <div className={"l-full-row " + bgColorClass + (!!addBoxShadowClass && "u-padding-top-m ")}>
                    <div className={"l-page-row " + (!!$("#simCountLoopMonitor").length == 0 && " u-padding-m")}>
                        <div
                            className={" g-white1-bg " + (!!addBoxShadowClass && " u-box-shadow--s ")}>
                            <div
                                className={"l-row u-padding-top-l " + (!!addBoxShadowClass && " u-padding-l u-padding-left-l u-padding-right-l")}>
                                {!!this.props.isLogged &&
                                <div className="l-col l-col-9 ">
                                    {
                                        this.props.loggedCustomerData.tradingName &&
                                        <p className="u-inline-block u-small-block">
                                            <span
                                                className="u-margin-right-s u-font-bold">{this.props.loggedCustomerData.tradingName} </span>
                                            {
                                                this.props.loggedCustomerData.accountCode && !this.isFix() &&
                                                <span className="u-margin-right-s u-margin-left-s u-small-hide">/</span>
                                            }
                                        </p>
                                    }
                                    {
                                        (this.props.loggedCustomerData.lastName || this.props.loggedCustomerData.firstName) &&
                                        <p className="u-inline-block u-small-block">
                                            {this.props.channels.sales === "WWW" &&

                                            <span className="u-margin-right-s">Witaj</span>
                                            }
                                            <span
                                                className="u-margin-right-s u-font-bold">{this.props.loggedCustomerData.firstName} {!this.isFix() && this.props.loggedCustomerData.lastName}</span>
                                            <span
                                                className="u-margin-right-s u-margin-left-s u-small-hide">{this.props.loggedCustomerData.accountCode && !this.props.isFix && !this.props.onlyAssignment && "/"}</span>
                                        </p>
                                    }
                                    {!this.isFix() && !this.props.onlyAssignment &&
                                    <div className="u-inline-block u-small-block">
                                        <OraTooltip
                                            maxWidth="600"
                                            label={this._renderTooltipLabel()}>
                                            {tooltipContent}
                                        </OraTooltip>
                                    </div>
                                    }
                                </div>
                                }
                                {!!this.props.isLogged && this._renderLogout()}
                                {!!showLogin &&
                                <div
                                    className={!!addBoxShadowClass ? " u-padding-left-l u-padding-right-l" : " u-padding-left-m u-padding-right-m"}>
                                    <MulticartCustomerLoginLink
                                        channels={this.props.channels}
                                        descriptions={this.props.descriptions}
                                        title={this.props.descriptions.logInTitle}/>
                                </div>
                                }
                            </div>
                        </div>
                        <LogoutConfirmationModal {...this.props} descriptions={{}}/>
                        <ProcessAlertModal {...this.props} />
                    </div>
                </div>
            )
        }
        return null
    }


}

const mapStateToProps = state => ({
    isFix: isCartFix(state) || isLandingPage(state) || isPreLandingPage(state) ,
    isFixLP: isLandingPage(state),
    loggedCustomerData: getLoggedCustomerData(state),
    isRegisterBillingAccountPopupB2B: getRegisterBillingAccountPopupB2B(state),
    showChangeAccount: !getDeliveryAndPaymentStep(state) && (!getIsCartStep(state) || hasEntries(state)),
    isCartStep: getIsCartStep(state),
    isDeliveryAndPaymentStep: getDeliveryAndPaymentStep(state),
    isCustomerDataStep: getIsCustomerDataStep(state),
    isProductListPage: isProductListPage(state),
    isAccessoryListPage: isAccessoryListPage(state),
    isProductDetailsPage: isProductDetailsPage(state),
    isLogged: isLogged(state),
    isCartMobile: isCartMobile(state),
    onlyAssignment: hasOnlyAssignment(state),
});

const mapDispatchToProps = dispatch => ({
    requestLoggedCustomerDataAndCheckExistence: () => dispatch(requestLoggedCustomerDataAndCheckExistence()),
    requestLoggedCustomerData: () => dispatch(requestLoggedCustomerData()),
    safeLogOut: () => dispatch(safeLogOut()),
    hideLogoutConfirmationModal: () => dispatch(hideLogoutConfirmationModal()),
    showMobileAccountSelection: () => dispatch(showMobileAccountSelection()),
    updateIsLandingPage: isLP => dispatch(updateIsLandingPage(isLP)),
});

export default connect(mapStateToProps, mapDispatchToProps)(LoggedCustomerBarComponent);
